import React, { useEffect, useState, useCallback } from 'react';
import ProjectItem from '../ProjectItem';
import '../../App.css';


const Project = () => {
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);

    const fetchProjects = useCallback(async () => {
        const projects = await fetch("https://api.github.com/users/treyham91/repos?sort=updated", {
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            }
        })

        return projects.json();
    }, [])

    useEffect(() => {
        setLoading(true)
        fetchProjects()
            .then(project => {
                console.log(project)
                setData(currData => currData.concat(project))
            })
            .catch(err => console.log(err));
        setLoading(false)
        }, [fetchProjects]);

    return (
        <div id="project-container">
            <p style={{textAlign: 'center', fontSize: 26}}>Projects</p>
            <div style={{padding: 20, borderBottom: '1px solid rgba(133, 133, 133, 0.2)'}}>
                <p style={{fontSize: 20}}>Freelance Projects:</p>
                <div className="project-item" style={{border: 'none'}}>
                    <a href="https://www.6to1designs.com" rel="noopener noreferrer" target="_blank">6to1 Designs</a>
                </div>
            </div>
            <p style={{padding: 25, fontSize: 20}}>GitHub Projects:</p>
            {loading ? 'Retrieving Projects...' :
                <div id="project-items">
                        {data.slice(0,5).map(repo => {
                            return (
                                <ProjectItem
                                    key={repo.id}
                                    name={repo.name}
                                    url={repo.html_url}
                                    description={repo.description}
                                    createdDate={repo.created_at}/>
                            )
                        })}
                </div>}
        </div>
    );
}


export default Project;