import React from 'react';
import Section from '../Pages/Sections/Section';
import face from '../../static/images/trey_face.png'
import '../../App.css';

const Home = () => {
    return (
        <Section containerId="home-body-container">
            <div style={{display: 'block', width: '40%'}}>
                <h1>Trey Hamilton</h1>
                <h3>Software Developer</h3>
                <p>Hey! My name is Trey, and I'm a software developer living and working in the Twin Cities that 
                    always find a few hours everyday to code things I love.</p>
            </div>
            <div>
                <img style={{height: 400, width: 400}} src={face} alt="Trey Face" />
            </div>
        </Section>
    );
}

export default Home;