import React from 'react';
import MenubarItem from './MenubarItem';
import MobileMenubar from './MobileMenubar';
import Media from 'react-media';
import '../../App.css';


const menuBarItems = [
    { 'key': 'home', 'to': '/', 'name': 'Home' },
    { 'key': 'about', 'to': 'about', 'name': 'About' },
    { 'key': 'projects', 'to': 'projects', 'name': 'Projects' },
    { 'key': 'skills', 'to': 'skills', 'name': 'Skills' },
]


const Menubar = (props) => {

    return (
        <React.Fragment>
            <Media query="(max-width: 830px)">
                {matches => (
                    matches ? ( <MobileMenubar /> ) : (
                    <div id="menubar-container">
                        <div style={{padding: 20}}>
                            {menuBarItems.map(item => {
                                return (
                                    <MenubarItem key={item.key} to={item.to} name={item.name} />
                                )
                            })}
                        </div>
                    </div> ) 
                )}
            </Media>
        </React.Fragment>
    );
}

export default Menubar;