import React, {useState, useEffect, useCallback} from 'react';
import config from '../../config/config';
import '../../App.css';

const About = () => {
    const [data, setData] = useState([]);

    const getMediumPublications = useCallback(async () => {
        const publications = await fetch(`https://api.medium.com/v1/users/${config.userId}/publications`, {
            method: 'GET',
            headers: {
                'Authorization': `Bearer ${config.accessToken}`,
                'Content-Type': 'application/json'
            }
        })

        return publications.json();
    }, [])

    useEffect(() => {
        getMediumPublications()
            .then(publication => {
                setData(currData => currData.concat(publication))
            })
            .catch(err => console.log(err))
    }, [getMediumPublications])

    useEffect(() => {
        console.log(data)
    }, [data])

    return (
        <div id="about-container">
            <div style={{borderBottom: '1px solid rgba(133, 133, 133, 0.2)', padding: 50}}>
                <p id="about-header">About Me</p>
                <div id="about-text">
                    Hi! My name is Trey Hamilton, and welcome to my page! I'm 29 years old, living in the twin cities with my wife,
                    along with our dog and cat, Brody and Todd. I grew up in a small farm town in Wisconsin, and still find myself acclimating to living in the city!
                    As far as my technical background, most of my experience is in the .NET framework, although I have worked on other projects outside of that scope.
                    Most recently, I built a new company portal using React.js, which runs on top of a Node.js server running express. It is being used by the entire North
                    American division of the company, and is likely to be adopted by other company locations around the globe. It is always fun building a piece of software
                    that others are eager to use!
                </div>
            </div>
            <div>
                <p style={{fontSize: 26, padding: 20}}>Personal Blog</p>
                <div id="blog-posts">
                    {data.length > 0 
                    ? data.map(post => {
                        return <p>{post}</p>
                    })
                    :<div><p style={{color: 'rgba(133, 133, 133, 0.3)'}}>No posts to view</p></div>}
                </div>
            </div>
        </div>
    );
}

export default About;