import React from 'react';
import '../App.css';

const ProjectItem = ({name, url, description, createdDate}) => {

    return (
        <div className="project-item">
            <p style={{fontSize: 17, color: 'rgb(24, 24, 24)', textDecoration: 'underline'}}>{name}</p>
            <p>{description}</p>
            <p style={{fontStyle: 'italic'}}>Created: {new Date(createdDate).toDateString()}</p> 
            <a href={url} rel="noopener noreferrer" target="_blank">View Project &gt;</a>
        </div>
    );
}

export default ProjectItem;