import React from 'react';
import '../../App.css';


const Skills = () => {
    return (
        <div style={{padding: 50}}>
            <div style={{padding: 30}}>
                <p style={{fontSize: 26, textAlign: 'center'}}>Skills</p>
            </div>
            <div id="skills-container">
                <div id="databases">
                    <p style={{fontSize: 20, marginBottom: 3, textDecoration: 'underline'}}>Databases</p>
                    <ul>
                        <li>SQL Server</li>
                        <li>MongoDB</li>
                        <li>MySQL</li>
                        <li>Oracle</li>
                    </ul>
                </div>
                <div id="languages">
                    <p style={{fontSize: 20, marginBottom: 3, textDecoration: 'underline'}}>Languages</p>
                    <ul>
                        <li>Python</li>
                        <li>JavaScript</li>
                        <li>SQL</li>
                        <li>PHP</li>
                        <li>C#</li>
                        <li>TypeScript</li>
                    </ul>
                </div>
                <div id="technologies">
                    <p style={{fontSize: 20, marginBottom: 3, textDecoration: 'underline'}}>Technologies</p>
                    <ul>
                        <li>React.js</li>
                        <li>Node.js</li>
                        <li>Vue.js</li>
                        <li>Django</li>
                        <li>HTML</li>
                        <li>CSS</li>
                        <li>REST APIs</li>
                        <li>.NET</li>
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Skills;