import React, { useState } from 'react';
import MenubarItem from './MenubarItem';
import { Link } from 'react-router-dom';


const menuBarItems = [
    { 'key': 'home', 'to': '/', 'name': 'Home' },
    { 'key': 'about', 'to': '/about', 'name': 'About' },
    { 'key': 'projects', 'to': '/projects', 'name': 'Projects' },
    { 'key': 'skills', 'to': '/skills', 'name': 'Skills' },
]


const Menubar = (props) => {
    const [menubar, setMenubar] = useState(false);

    const setMenubarState = () => {
        if (!menubar) {
            setMenubar(true)
        }

        else {
            setMenubar(false)
        }
    }

    return (
        <React.Fragment>
            <div id="mobile-menubar-container">
                <button onClick={setMenubarState} id="mobile-menu-button">
                    <div className="mobile-menu-line"></div>
                    <div className="mobile-menu-line"></div>
                    <div className="mobile-menu-line"></div>
                </button>
            </div>
            {menubar ?
                <div id="mobile-menubar-item-container">
                    <ul id="mobile-menubar-item-list">
                        {menuBarItems.map(item => {
                            return (
                                <li key={item.key + '-li'} id="mobile-menubar-list-item">
                                    <React.Fragment>
                                        <Link onClick={() => setMenubar(false)} id="mobile-menubar-item" key={item.key} to={item.to}>{item.name}</Link>
                                    </React.Fragment></li>
                            )
                        })}
                    </ul>
                </div> : null}
        </React.Fragment>
    );
}

export default Menubar;